<template>
	<view class="nav-bar">
		<!-- 导航栏 -->
		<view class="nav-bar-top">
			<view :style="{height: startHeight + 'rpx'}"></view>
			<view class="nav-bar-content" :style="{marginRight: startRight + 'rpx'}" @click="goSearchPage">
				<uni-icons type="search" color="#999"></uni-icons>
				<view class="nav-bar-search-txt">
					输入文章标题进行搜索
				</view>
			</view>
		</view>

		<!-- 底部的垫片 -->
		<view :style="{height: startHeight + 80 + 'rpx'}">

		</view>
	</view>
</template>

<script>
	export default {
		name: "Navbar",
		created() {
			this.getSystemInfo()
		},
		data() {
			return {
				startRight: 0,
				startHeight: 20
			}
		},
		methods: {
			async getSystemInfo() {
			   /* 导航栏顶部padding取默认值*/
			   	const systemInfo = await uni.getSystemInfoSync()
				systemInfo.statusBarHeight && (this.startHeight = systemInfo.statusBarHeight * 2)
				
				// #ifdef MP-WEIXIN
				const menuButtonInfo = uni.getMenuButtonBoundingClientRect()
				/* 导航栏在小程序中顶部padding取胶囊top值*/
				this.startHeight = menuButtonInfo.top * 2
				
				menuButtonInfo.width && (this.startRight = menuButtonInfo.width * 2)
				// #endif
			},
			// 跳转到搜索页面
			goSearchPage(){
				uni.navigateTo({
					url:'/pages/search/search'
				})
			}
		},
	}
</script>

<style lang="scss" scoped>
	@import './css/Navbar.scss'
</style>
